.timer-container {
  text-align: center;
  margin: 20px auto;
  padding: 20px;
  border: 2px solid #333;
  border-radius: 10px;
  width: 300px;
  background-color: #f4f4f4;
}

.timer-display {
  font-size: 2em;
  font-weight: bold;
  margin-bottom: 20px;
}

.timer-controls {
  display: flex;
  justify-content: center;
  gap: 10px;
}

.timer-button {
  padding: 10px 15px;
  font-size: 16px;
  font-weight: bold;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.timer-button:hover {
  opacity: 0.9;
}

.timer-button:active {
  transform: scale(0.98);
}

.timer-button.complete {
  background-color: #4caf50;
  color: white;
}

.timer-button.complete:hover {
  background-color: #45a049;
}

.timer-button {
  background-color: #007bff;
  color: white;
}
